.ourClientsMainContainer {
  display: flex;
  flex-direction: row;
  margin: .5rem;
  padding: .5rem;
}

.ourClientsInformation {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
}

.ourClientsInfoTitle {
  font-size: 1.5rem;
}

.ourClientsInfoRegText {
  font-size: 1.15rem;
}

@media screen and (max-width: 840px) {
  .ourClientsMainContainer {
    flex-direction: column;
  }
}

@media screen and (max-width: 550px) {
  .ourClientsImage img {
    width: 325px;
  }
}