.aboutMainContainer {
  display: flex;
  flex-direction: row;
  margin: .5rem;
  padding: .5rem;
}

.aboutInformation {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
}

.aboutInfoTitle {
  font-size: 1.5rem;
}

.aboutInfoRegText {
  font-size: 1.15rem;
}

@media screen and (max-width: 840px) {
  .aboutMainContainer {
    flex-direction: column;
  }
}

@media screen and (max-width: 550px) {
  .aboutImage img {
    width: 325px;
  }
}