.navBarMainContainer {
  display: flex;
  justify-content: space-between;
  height: 100px;
  width: 100%;
  border-bottom: 1px grey solid;
}

.navBarLogo {
  display: flex;
  align-items: center;
  padding: .5rem;
}

.navBarLogo img {
  max-height: 90px;
}

.navBarMainLinks {
  display: flex;
  align-items: center;
  padding: .5rem;
}

.navBarMainLinks a {
  padding: .5rem;
  font-size: 1.5rem;
  font-weight: 700;
  font-family: 'Baloo Chettan 2', cursive;
}

.navBarSideLinks {
  display: flex;
  align-items: center;
  padding: .5rem;
  font-family: 'Baloo Chettan 2', cursive;
}

.navBarSideLinks a {
  padding: .5rem;
  font-size: 1.5rem;
  font-weight: 700;
}